@import url("https://fonts.googleapis.com/css?family=Open+Sans:300,400,700&display=swap");
* {
  box-sizing: border-box;
}

body {
  margin: 0;
  font-family: 'open-sans', sans-serif;
}

img {
  width: 100%;
}

strong {
  font-weight: 700;
}

a {
  color: inherit;
  text-decoration: none;
}

a, p {
  font-family: 'proxima-nova', sans-serif;
}

.btn_ywllow_vmer {
  background-color: #F6AA2A;
  padding: 15px;
  display: table;
}

.btn_ywllow_mre_brder {
  border: 2px solid #F6AA2A;
  padding: 15px;
  display: table;
  color: #F6AA2A;
  font-size: 12px;
  line-height: 15px;
  font-weight: 700;
  text-transform: uppercase;
}

.container_wide {
  max-width: 1250px;
  width: 100%;
  margin: 0 auto;
}

@media only screen and (max-width: 1410px) {
  .container_wide {
    padding-left: 15px;
    padding-right: 15px;
  }
}

.container {
  max-width: 1105px;
  width: 100%;
  margin: 0 auto;
}

@media only screen and (max-width: 1410px) {
  .container {
    padding-left: 15px;
    padding-right: 15px;
  }
}

h1, h2, h3, h4, h5, h6 {
  margin: 0;
  font-family: 'proxima-nova', sans-serif;
}

header {
  position: fixed;
  z-index: 199;
  width: 100%;
  top: 0;
  transition: 0.75s all;
  webkit-transition: 0.75s all;
}

header .container_wide {
  height: 110px;
  display: flex;
  justify-content: flex-end;
  align-items: center;
}

header .container_wide nav a {
  text-decoration: none;
  color: #666666;
  font-weight: 700;
  font-size: 16px;
  text-transform: uppercase;
  margin-right: 10px;
  transition: 0.75s all;
  webkit-transition: 0.75s all;
}

header .container_wide nav a:last-child {
  margin-right: 0;
}

.collapsed_header {
  background-color: rgba(255, 255, 255, 0.875);
}

.collapsed_header nav a {
  color: #F6AA2A !important;
}

.index_wrap {
  height: 100vh;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}

.index_wrap .container {
  position: relative;
  z-index: 5;
}

.index_wrap .container h1 {
  font-weight: 400;
  color: #DDDDDD;
  font-size: 46px;
  margin-bottom: 45px;
}

.index_wrap .container h1 strong {
  display: block;
}

.banner_inner {
  height: 40vh;
  width: 100%;
  position: relative;
  display: flex;
  align-items: flex-end;
  justify-content: center;
  margin-bottom: 75px;
}

.banner_inner h1 {
  font-weight: 400;
  color: #DDDDDD;
  font-size: 46px;
  margin-bottom: 45px;
}

.banner_inner h1 strong {
  display: block;
}

#search_wrap {
  margin: 0 auto;
  padding: 0;
  max-width: 1105px;
  width: 100%;
  background-color: #F8F8F8;
  z-index: 99;
  box-shadow: 0 2px 30px 0 rgba(0, 0, 0, 0.35);
}

#search_wrap .top_bar_search {
  background-color: #F8F8F8;
  width: 100%;
  height: 50px;
  display: flex;
  align-items: center;
  padding: 0 25px;
}

#search_wrap .top_bar_search h4 {
  color: #F6AA2A;
  font-size: 13;
  font-weight: 700;
  letter-spacing: 2px;
}

#search_wrap form {
  display: flex;
  height: 80px;
  background-color: #FFFFFF;
  justify-content: space-between;
  width: 100%;
  position: relative;
  padding-left: 25px;
  color: #AFAFAF;
}

#search_wrap form .address_entry {
  max-width: 60%;
  width: 100%;
  border-right: 1px solid #F8F8F8;
}

#search_wrap form .radius_entry {
  max-width: 12%;
  width: 100%;
  border-right: 1px solid #F8F8F8;
}

#search_wrap form .bed_entry {
  max-width: 16%;
  width: 100%;
}

#search_wrap form input, #search_wrap form select {
  background-color: transparent;
  border: 0;
  border-radius: 0 !important;
  font-size: 18px;
  color: #AFAFAF !important;
  font-weight: 400;
  -webkit-appearance: none;
  font-family: 'proxima-nova', sans-serif !important;
}

#search_wrap form select {
  background: url("../images/grey_dropdown.png") no-repeat 90% transparent;
}

#search_wrap form ::-webkit-input-placeholder {
  color: #AFAFAF !important;
}

#search_wrap form ::-moz-placeholder {
  color: #AFAFAF !important;
}

#search_wrap form :-ms-input-placeholder {
  color: #AFAFAF !important;
}

#search_wrap form :-moz-placeholder {
  color: #AFAFAF !important;
}

#search_wrap form .submitter_maps {
  background-color: #F6AA2A;
  max-width: 80px;
  width: 100%;
  position: relative;
  border: 0;
}

#search_wrap form .submitter_maps img {
  max-width: 32px;
}

#results_search {
  margin-top: 80px;
}

#results_search #map_wrapper {
  height: 1000px;
  width: 100%;
  display: flex;
  position: relative;
  overflow: hidden;
}

#results_search #map_wrapper .radius_results {
  width: 60%;
  padding: 15px;
  position: relative;
}

#results_search #map_wrapper .radius_results .innerWrapResult {
  overflow: scroll;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  padding: 15px;
}

#results_search #map_wrapper .radius_results .innerWrapResult .itemWrapper {
  max-width: 1000px;
  width: 100%;
  margin: 0 auto 25px auto;
  display: flex;
  align-items: center;
  justify-content: space-between;
  position: relative;
}

#results_search #map_wrapper .radius_results .innerWrapResult .itemWrapper .img_wrapper {
  width: 75%;
  height: 500px;
  margin-right: 15px;
  background-image: url("../images/feature-dev-example.png");
  background-size: cover;
  background-size: cover;
}

#results_search #map_wrapper .radius_results .innerWrapResult .itemWrapper .content_wrapper {
  max-width: 400px;
  width: 100%;
  margin-left: -50%;
  background: #F8F8F8;
  box-shadow: 0 2px 30px 0 rgba(0, 0, 0, 0.35);
  webkit-box-shadow: 0 2px 30px 0 rgba(0, 0, 0, 0.35);
  position: relative;
  z-index: 2;
}

#results_search #map_wrapper .radius_results .innerWrapResult .itemWrapper .content_wrapper .bg_inner {
  background-color: #F8F8F8;
  padding: 25px;
}

#results_search #map_wrapper .radius_results .innerWrapResult .itemWrapper .content_wrapper .bg_inner span {
  color: #F6AA2A;
  font-size: 13px;
  letter-spacing: 2px;
  font-weight: 700;
}

#results_search #map_wrapper .radius_results .innerWrapResult .itemWrapper .content_wrapper .bg_inner h3 {
  font-size: 36px;
  line-height: 44px;
  font-weight: 300;
  color: #565656;
  margin-bottom: 15px;
}

#results_search #map_wrapper .radius_results .innerWrapResult .itemWrapper .content_wrapper .bg_inner h4 {
  font-size: 18px;
  line-height: 22px;
  font-weight: 300;
  color: #565656;
}

#results_search #map_wrapper .radius_results .innerWrapResult .itemWrapper .content_wrapper .bg_inner p {
  font-size: 15px;
  line-height: 26px;
  color: #565656;
}

#results_search #map_wrapper .radius_results .innerWrapResult .itemWrapper .content_wrapper .bg_inner .location_title {
  position: relative;
  padding: 5px 0 10px 0;
}

#results_search #map_wrapper .radius_results .innerWrapResult .itemWrapper .content_wrapper .bg_inner .price_title {
  position: relative;
  padding: 5px 0 10px 0;
}

#results_search #map_wrapper .radius_results .innerWrapResult .itemWrapper:last-child {
  margin-bottom: 0;
}

#results_search #map_wrapper .map_result {
  height: 100%;
  width: 40%;
}

#results_search #map_wrapper .map_result #map_canvas {
  width: 100%;
  height: 100%;
}

#results_search #map_wrapper .overlay_map_result {
  background-color: #FFFFFF;
  position: absolute;
  z-index: 10;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}

#results_search #map_wrapper .overlay_map_result .pulse-loader {
  height: 50px;
  margin: auto 0;
  position: relative;
  width: 50px;
}

#results_search #map_wrapper .overlay_map_result .pulse-loader:before, #results_search #map_wrapper .overlay_map_result .pulse-loader:after {
  animation-name: pulse-loader;
  animation-iteration-count: infinite;
  animation-duration: 2s;
  box-shadow: 0 0 5px #F6AA2A;
  border-radius: 50%;
  border: 3px solid #F6AA2A;
  content: '';
  display: block;
  height: 100%;
  left: 0;
  opacity: 0;
  position: absolute;
  top: 0;
  width: 100%;
}

#results_search #map_wrapper .overlay_map_result .pulse-loader:after {
  animation-delay: 1s;
}

@keyframes pulse-loader {
  0% {
    opacity: 0;
    transform: scale(0);
  }
  60% {
    opacity: 1;
  }
  100% {
    opacity: 0;
    transform: scale(1);
  }
}

/* Just use .gm-style-iw */
.gm-style-iw {
  width: 300px;
  min-height: 150px;
  text-align: center;
}
